<template>
	<div class="insureResult" v-if="isShow">
		<div class="result clearfix">
			<div class="t">
				<div class="tt" v-if="result.code">
					<img src="../assets/images/success.png">
					<div>投保成功！</div>
				</div>
				<div class="tt fail" v-else>
					<img src="../assets/images/insurefail.png">
					<div>投保失败！</div>
				</div>
			</div>
			<span class="c-b" v-text="result.data.name" v-if="result.code"></span>
			<span class="c-s" v-if='result.code' v-text="status( result.data.status)"></span>
			<div class="sn" v-if='result.code'>保单号: {{result.data.policy_num}}</div>
			<div class="sn" v-if="!result.code">{{result.message}}</div>
			<div class="fr toDetail" @click="toDetail">查看订单详情</div>
			<div class="fr toIndex" @click="toIndex">返回首页</div>
		</div>
		<div class="others">
			<p class="p">关注37度C健康保险网官方微信，你将体验更多服务</p>
			<div class="lists clearfix">
				<ul>
					<li class="fl">
						<img src="../assets/images/insuerResult-insure.png">
						<p class="pp">随时随地快速投保</p>
					</li>
					<li class="fl">
						<img src="../assets/images/insureResult-settle.png">
						<p class="pp">线上申请理赔服务</p>
					</li>
					<li class="fl">
						<img src="../assets/images/insureResult-response.png">
						<p class="pp">获取更多保险资讯</p>
					</li>
				</ul>
			</div>
			<div class="code">
				<img src="../assets/images/code.png">
				<p>扫一扫，关注官方微信</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				isShow: false,
				result: {}
			}
		},
		methods: {
			status(status){
				let res;
				switch(status){
					case 0: res='待支付';break;
					case 1: res='已支付';break;
					case 2: res='已承保';break;
					case 3: res='已出单';break;
					case 4: res='保障中';break;
					case 5: res='已失效';break;
					case 6: res='订单失败';break;
					case 7: res='已取消';break;
				}
				return res
			},
			toDetail(orderSn){
				this.$router.push({
					path: '/37duc/personCenter/orderDetail/'+this.$route.params.orderSn,
					query: {
						orderId: this.$route.params.orderSn
					}
				})
			},
			toIndex(){
				this.$router.push('/');
			}
		},
		mounted(){
			this.$api.product.getInsuranceResult({
				orderSn: this.$route.params.orderSn
			})
			.then(data=>{
				this.result = JSON.parse(JSON.stringify(data))
				this.isShow = true
			})
		}
	}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/mixin.scss';
.insureResult{
	.result{
		box-shadow: 0px 2px 4px 0 rgba(219,219,219,.35);
		padding: 50px 40px 0;
		background: #FFFFFF;
		.t{
			position: relative;
			margin: 0 0 106px;
			.tt{
				height: 50px;
				font-size: 24px;
				line-height: 50px;
				color: $color-active-text;
				font-weight: 600;
				text-align: center;
				width: 200px;
				margin-left: -100px;
				position: absolute;
				left: 50%;
				img{
					width: 50px;
					height: 50px;
					float: left;
				}
				&.fail{
					color: #FF4B4B;
				}
			}
		}
		.c-b, .c-s{
			vertical-align: center;
			color: $color-text;
			line-height: 24px;
		}
		.c-b{
			font-size: 24px;
			font-weight: 600;
			padding-right: 30px;
		}
		.c-s{
			font-size: 16px;
		}
		.sn{
			word-break: break-all;
			margin: 30px 0 48px;
			font-size: 16px;
			line-height: 16px;
			color: #A5A8A8;
		}
		.fr{
			width: 160px;
			margin: 48px 0 45px 20px;
			border-radius: 22px;
			line-height: 42px;
			font-size: 16px;
			text-align: center;
			border: 1px solid $color-active-text;
			&.toDetail{
				color: #FFFFFF;
				background: $color-active-text;
			}
			&.toIndex{
				color: $color-active-text;
				background: #FFFFFF;
			}
		}
	}
	.others{
		padding: 30px 40px 19px;
		background: #FAFAFA;
		position: relative;
		.p{
			line-height: 16px;
			font-size: 14px;
			color: #A5A8A8;
			margin-bottom: 33px;
		}
		.lists{
			margin-top: 1px;
			li{
				margin-right: 39px;
				img{
					display: block;
					margin: 0 auto;
					width: 44px;
					height: 44px;
					margin-bottom: 29px;
				}
				.pp{
					line-height: 16px;
					font-size: 12px;
					color: #A5A8A8;
				}
			}
		}
		.code{
			position: absolute;
			right: 40px;
			top: 30px;
			img{
				display: block;
				margin: 1px auto 21px;
			}
			p{
				line-height: 17px;
				font-size: 12px;
				color: $color-text;
			}
		}
	}
}
</style>
